@blue_color : #40a9ff;
@blue_active_color : #0050b3;
@blue_hover_color: #bae7ff;

@green_color : #52c41a;
@green_active_color: #389e0d;
@green_hover_color: #d9f7be;

@orange_color : #ffc53d;
@orange_active_color : #d46b08;
@orange_hover_color: #ffe7ba;

@pink_color : #ffadd2;
@pink_active_color : #ff85c0;
@pink_hover_color: #ffd6e7;

@cyan_color : #13c2c2;
@cyan_hover_color: #b5f5ec;
@cyan_active_color : #1facb4;

.theme(@bg, @bg_active, @bg_hover) {
    header {
        background: @bg;
    }

    .event-tools {
        .tools-btn {
            border: 1px solid @bg;
            background: @bg;

            &:hover {
                background: @bg_active;
            }
        }
    }

    .event-add {

        .n-input {
            caret-color: @bg;
            &:focus {
                border-color: @bg;
            }
        }


        .add-btn {
            border: 1px solid @bg;
            background: @bg;

            &:hover {
                background: @bg_active;
            }
        }
    }

    .event-content {
        .event-tab {
            background: @bg;
        }

        .event-box {
            .event-list {

                &:hover {
                    background-color: @bg_hover;
                }

                .cancel-btn:hover {
                    border: 1px solid @bg;
                    color: @bg;
                }
            }
        }
    }

    .event-all {
        .table-box {

            .edit-input,
            .event-table {
                button {
                    background: @bg;
                }
            }
        }
    }

    .dialog {
        .dialog-btns {
            .sure-btn {
                border: 1px solid @bg;
                background: @bg;

                &:hover {
                    background: @bg_active;
                }
            }
        }
    }
}

//蓝色主题
.blue {
    .theme(@blue_color, @blue_active_color, @blue_hover_color)
}

// 绿色主题
.green {
    .theme(@green_color, @green_active_color, @green_hover_color)
}

// 黄色主题
.orange {
    .theme(@orange_color, @orange_active_color, @orange_hover_color)
}

// 粉色主题
.pink {
    .theme(@pink_color, @pink_active_color, @pink_hover_color)
}

// 青蓝色主题
.cyan {
    .theme(@cyan_color, @cyan_active_color, @cyan_hover_color)
}