.event-add {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    display: flex;
    justify-content: space-between;
    width: 90%;
    max-width: 800px;
    margin: 0 auto 10px;
    
    font-size: 16px;

    .n-input {
        width: 80%;
        height: 40px;
        padding: 7px 10px;
        line-height: 26px;
        border: 1px solid #c0ccda;
        border-radius: 4px;
        transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
        box-sizing: border-box;
        font-size: inherit;

        &:focus {
            outline: none;
        }
    }

    .add-btn {
        width: 80px;
        height: 40px;
        margin-left: 20px;
        line-height: 26px;
        color: #fff;
    }
}